
.form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: rgb(239 255 250);
  height: 100vh;
}
.left-side {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.operators button {
  margin: 0 20px;
}
.input_area {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 20px;
}
.form input {
  outline: none;
  border: 2px solid rgb(195 90 221);
  border-radius: 5px 0 0 5px;
  padding: 10px;
  border-right: none;
}
.form input.incrementNumber {
  background-color: #efefef;
  width: 10%;
  margin-right: 10 px;
  border-right: 2 px solid #c35add;
  border-radius: 4 px;
}
.input_area span {
  padding: 10px 10px;
  background: rgb(226 226 226);
  border-radius: 5px 0 0 5px;
  font-size: 20px;
}
button {
  color: white;
  background: rgb(195 90 221);
  padding: 10px 10px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
}
button.active {
  background: #0099d3;
}
button.btn-danger {
  background: #ff0faf;
}
button.inactive {
  background: #cdcdcd;
}
.input_area button {
  border-radius: 0 5px 5px 0;
}
button:disabled {
  background: rgb(199, 199, 199);
}
.form ul {
  height: 400px;
  overflow: scroll;
}
ul li {
  font-size: 20pt;
}
input.valid {
  border-color: green;
}
input.invalid {
  border-color: red;
  background-color: rosybrown;
}
.valid {
  color: rgb(25, 87, 255);
}



